// apps/dashboard/app/bookings/page.tsx

"use client";
import React, { useState, useEffect, useRef } from 'react';
import Link from 'next/link';
import { useAuth } from '@/lib/useAuth';
import { useBookingNotifications } from '../components/BookingNotificationContext';

interface Booking {
    id: string;
    shop_id: string;
    service_id: string;
    staff_id: string;
    customer_id: string;
    customer_name?: string;
    customer_email?: string;
    customer_phone?: string;
    date?: string;
    time?: string;
    start_time: string;
    end_time: string;
    notes: string;
    status: 'pending' | 'confirmed' | 'rejected' | 'cancelled' | 'completed';
    shops?: { name: string } | null;
    services?: { name: string } | null;
    staff?: { first_name: string; last_name: string } | null;
}

interface Shop {
    id: string;
    name: string;
}

interface Service {
    id: string;
    name: string;
}

interface Staff {
    id: string;
    first_name: string;
    last_name: string;
}

interface Timeslot {
    id: string;
    start_time: string;
    end_time: string;
}

const BookingsPage = () => {
    const { user } = useAuth();
    const { setUnreadBookingsCount } = useBookingNotifications();
    const [bookings, setBookings] = useState<Booking[]>([]);
    const [shops, setShops] = useState<Shop[]>([]);
    const [services, setServices] = useState<Service[]>([]);
    const [staffMembers, setStaffMembers] = useState<Staff[]>([]);
    const [timeslots, setTimeslots] = useState<Timeslot[]>([]);
    const [newBooking, setNewBooking] = useState({
        shop_id: '',
        service_id: '',
        staff_id: '',
        timeslot_id: '',
        start_time: '',
        end_time: '',
        notes: '',
    });
    const [updatingStatus, setUpdatingStatus] = useState<string | null>(null);
    const [editingBooking, setEditingBooking] = useState<Booking | null>(null);
    const [editStartTime, setEditStartTime] = useState<string>('');
    const [editEndTime, setEditEndTime] = useState<string>('');
    const [showEditModal, setShowEditModal] = useState(false);
    const [openDropdown, setOpenDropdown] = useState<string | null>(null);
    const dropdownRefs = useRef<{ [key: string]: HTMLDivElement | null }>({});
    const apiUrl = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3000';

    // Reset unread count when page loads
    useEffect(() => {
        setUnreadBookingsCount(0);
    }, [setUnreadBookingsCount]);

    // Close dropdown when clicking outside
    useEffect(() => {
        if (!openDropdown) return;
        
        const handleClickOutside = (event: MouseEvent) => {
            const target = event.target as Element;
            const dropdownElement = dropdownRefs.current[openDropdown];
            if (dropdownElement && !dropdownElement.contains(target)) {
                setOpenDropdown(null);
            }
        };
        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, [openDropdown]);

    useEffect(() => {
        if (!user) return;

        const fetchAll = async () => {
            try {
                const headers: HeadersInit = {
                    'x-user-id': user.id,
                };

                // Fetch all shops
                const shopsRes = await fetch(`${apiUrl}/shops`);
                if (shopsRes.ok) {
                    const shopsData = await shopsRes.json();
                    setShops(Array.isArray(shopsData) ? shopsData : []);
                } else {
                    console.error("Failed to fetch shops:", shopsRes.status, shopsRes.statusText);
                    setShops([]);
                }

                // Fetch services (filtered by user's shops)
                const servicesRes = await fetch(`${apiUrl}/services`, { headers });
                if (servicesRes.ok) {
                    const servicesData = await servicesRes.json();
                    setServices(Array.isArray(servicesData) ? servicesData : []);
                } else {
                    console.error("Failed to fetch services:", servicesRes.status, servicesRes.statusText);
                    setServices([]);
                }

                // Fetch staff (filtered by user's shops)
                const staffRes = await fetch(`${apiUrl}/staff`, { headers });
                if (staffRes.ok) {
                    const staffData = await staffRes.json();
                    setStaffMembers(Array.isArray(staffData) ? staffData : []);
                } else {
                    console.error("Failed to fetch staff:", staffRes.status, staffRes.statusText);
                    setStaffMembers([]);
                }

                // Fetch timeslots (filtered by user's shops)
                try {
                    const timeslotsRes = await fetch(`${apiUrl}/timeslots`, { headers });
                    if (timeslotsRes.ok) {
                        const timeslotsData = await timeslotsRes.json();
                        setTimeslots(Array.isArray(timeslotsData) ? timeslotsData : []);
                    } else {
                        console.error("Failed to fetch timeslots:", timeslotsRes.status, timeslotsRes.statusText);
                        setTimeslots([]);
                    }
                } catch (timeslotError) {
                    console.error("Error fetching timeslots:", timeslotError);
                    setTimeslots([]);
                }

                // Fetch bookings (filtered by user's shops)
                try {
                    const bookingsRes = await fetch(`${apiUrl}/bookings`, { headers });
                    if (bookingsRes.ok) {
                        const bookingsData = await bookingsRes.json();
                        // Double-check that data is an array before setting state
                        if (Array.isArray(bookingsData)) {
                            setBookings(bookingsData);
                        } else {
                            console.error("Bookings data is not an array:", typeof bookingsData, bookingsData);
                            setBookings([]);
                        }
                    } else {
                        console.error("Failed to fetch bookings:", bookingsRes.status, bookingsRes.statusText);
                        // Try to get error message from response
                        try {
                            const errorData = await bookingsRes.json();
                            console.error("Error details:", errorData);
                        } catch (e) {
                            // Ignore JSON parse errors
                        }
                        setBookings([]);
                    }
                } catch (bookingError) {
                    console.error("Error fetching bookings:", bookingError);
                    setBookings([]);
                }
            } catch (error) {
                console.error("Failed to fetch data:", error);
                // Ensure all states are arrays even on error
                setShops([]);
                setServices([]);
                setStaffMembers([]);
                setTimeslots([]);
                setBookings([]);
            }
        };

        fetchAll();
    }, [apiUrl, user]);

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
        const { name, value } = e.target;
        setNewBooking(prev => ({ ...prev, [name]: value }));
    };

    // No date filtering - all bookings for owner's shops are shown
    const filteredBookings = bookings;

    const refreshBookings = async () => {
        if (!user?.id) return;
        try {
            const bookingsRes = await fetch(`${apiUrl}/bookings`, {
                headers: {
                    'x-user-id': user.id,
                },
            });
            if (bookingsRes.ok) {
                const bookingsData = await bookingsRes.json();
                if (Array.isArray(bookingsData)) {
                    setBookings(bookingsData);
                    const pendingCount = bookingsData.filter((b: Booking) => b.status === 'pending').length;
                    setUnreadBookingsCount(pendingCount);
                }
            }
        } catch (error) {
            console.error('Error refreshing bookings:', error);
        }
    };

    const confirmBooking = async (bookingId: string) => {
        if (!user?.id) return;
        setUpdatingStatus(bookingId);
        try {
            const res = await fetch(`${apiUrl}/bookings/${bookingId}`, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json',
                    'x-user-id': user.id,
                },
                body: JSON.stringify({ status: 'confirmed' }),
            });

            if (res.ok) {
                await refreshBookings();
            } else {
                const errorData = await res.json().catch(() => ({}));
                alert(errorData.error || 'Failed to confirm booking');
            }
        } catch (error) {
            console.error('Error confirming booking:', error);
            alert('Failed to confirm booking');
        } finally {
            setUpdatingStatus(null);
        }
    };

    const cancelBooking = async (bookingId: string) => {
        if (!user?.id) return;
        if (!confirm('Are you sure you want to cancel this booking?')) return;
        
        setUpdatingStatus(bookingId);
        try {
            const res = await fetch(`${apiUrl}/bookings/${bookingId}`, {
                method: 'DELETE',
                headers: {
                    'x-user-id': user.id,
                },
            });

            if (res.ok) {
                await refreshBookings();
            } else {
                const errorData = await res.json().catch(() => ({}));
                alert(errorData.error || 'Failed to cancel booking');
            }
        } catch (error) {
            console.error('Error canceling booking:', error);
            alert('Failed to cancel booking');
        } finally {
            setUpdatingStatus(null);
        }
    };

    const openEditModal = (booking: Booking) => {
        setEditingBooking(booking);
        // Convert ISO strings to datetime-local format
        const startDateTime = booking.start_time ? new Date(booking.start_time).toISOString().slice(0, 16) : '';
        const endDateTime = booking.end_time ? new Date(booking.end_time).toISOString().slice(0, 16) : '';
        setEditStartTime(startDateTime);
        setEditEndTime(endDateTime);
        setShowEditModal(true);
    };

    const saveReschedule = async () => {
        if (!user?.id || !editingBooking) return;
        
        if (!editStartTime || !editEndTime) {
            alert('Please provide both start and end times');
            return;
        }

        setUpdatingStatus(editingBooking.id);
        try {
            const res = await fetch(`${apiUrl}/bookings/${editingBooking.id}`, {
                method: 'PATCH',
                headers: {
                    'Content-Type': 'application/json',
                    'x-user-id': user.id,
                },
                body: JSON.stringify({
                    start_time: new Date(editStartTime).toISOString(),
                    end_time: new Date(editEndTime).toISOString(),
                }),
            });

            if (res.ok) {
                await refreshBookings();
                setShowEditModal(false);
                setEditingBooking(null);
                setEditStartTime('');
                setEditEndTime('');
            } else {
                const errorData = await res.json().catch(() => ({}));
                alert(errorData.error || 'Failed to reschedule booking');
            }
        } catch (error) {
            console.error('Error rescheduling booking:', error);
            alert('Failed to reschedule booking');
        } finally {
            setUpdatingStatus(null);
        }
    };

    const createBooking = async () => {
        try {
            const res = await fetch(`${apiUrl}/bookings`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(newBooking),
            });

            if (res.ok) {
                // Refresh bookings after creation
                try {
                    const bookingsRes = await fetch(`${apiUrl}/bookings`);
                    if (bookingsRes.ok) {
                        const bookingsData = await bookingsRes.json();
                        // Double-check that data is an array before setting state
                        if (Array.isArray(bookingsData)) {
                            setBookings(bookingsData);
                        } else {
                            console.error("Bookings data is not an array:", typeof bookingsData, bookingsData);
                            setBookings([]);
                        }
                    } else {
                        console.error("Failed to refresh bookings:", bookingsRes.status, bookingsRes.statusText);
                        setBookings([]);
                    }
                } catch (refreshError) {
                    console.error("Error refreshing bookings:", refreshError);
                    setBookings([]);
                }
                setNewBooking({
                    shop_id: '',
                    service_id: '',
                    staff_id: '',
                    timeslot_id: '',
                    start_time: '',
                    end_time: '',
                    notes: '',
                });
            } else {
                console.error("Failed to create booking:", res.status, res.statusText);
            }
        } catch (error) {
            console.error("Failed to create booking:", error);
        }
    };

    // Show empty state if user has no shops
    if (user && shops.length === 0) {
        return (
            <div className="p-8">
                <div className="mb-6">
                    <h1 className="text-3xl font-bold text-gray-800 mb-2">Bookings</h1>
                    <p className="text-gray-600">View and manage all bookings</p>
                </div>
                <div className="bg-white rounded-xl shadow-md border border-gray-100 p-12 text-center">
                    <div className="text-6xl mb-4">üè™</div>
                    <h2 className="text-2xl font-bold text-gray-800 mb-2">No Shops Found</h2>
                    <p className="text-gray-600 mb-6">
                        You need to create or claim a shop before you can view bookings.
                    </p>
                    <Link
                        href="/shops"
                        className="inline-block bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white font-semibold py-3 px-6 rounded-lg shadow-md hover:shadow-lg transition-all duration-200"
                    >
                        Go to Shops ‚Üí
                    </Link>
                </div>
            </div>
        );
    }

    return (
        <div className="p-8">
            <div className="mb-6">
                <h1 className="text-3xl font-bold text-gray-800 mb-2">Bookings</h1>
                <p className="text-gray-600">View and manage all bookings</p>
            </div>

            {/* Bookings Table */}
            <div className="bg-white rounded-xl shadow-md border border-gray-100 overflow-hidden">
                <div className="px-6 py-4 border-b border-gray-200 bg-gray-50">
                    <h2 className="text-xl font-bold text-gray-800">
                        All Bookings ({filteredBookings.length})
                    </h2>
                </div>
                        <div className="overflow-x-auto">
                            {Array.isArray(filteredBookings) && filteredBookings.length > 0 ? (
                                <table className="w-full">
                                    <thead className="bg-gray-50">
                                        <tr>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Customer</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Service</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Staff</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Start Time</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">End Time</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Status</th>
                                            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Actions</th>
                                        </tr>
                                    </thead>
                                    <tbody className="bg-white divide-y divide-gray-200">
                                        {filteredBookings.map(booking => {
                                            try {
                                                const startTime = booking.start_time ? new Date(booking.start_time).toLocaleString() : 'N/A';
                                                const endTime = booking.end_time ? new Date(booking.end_time).toLocaleString() : 'N/A';
                                                const isPending = booking.status === 'pending';
                                                const isUpdating = updatingStatus === booking.id;

                                                const getStatusBadge = (status: string) => {
                                                    const baseClasses = "px-2 py-1 text-xs font-semibold rounded-full";
                                                    switch (status) {
                                                        case 'pending':
                                                            return <span className={`${baseClasses} bg-yellow-100 text-yellow-800`}>Pending</span>;
                                                        case 'confirmed':
                                                            return <span className={`${baseClasses} bg-green-100 text-green-800`}>Confirmed</span>;
                                                        case 'rejected':
                                                            return <span className={`${baseClasses} bg-red-100 text-red-800`}>Declined</span>;
                                                        case 'cancelled':
                                                            return <span className={`${baseClasses} bg-gray-100 text-gray-800`}>Cancelled</span>;
                                                        case 'completed':
                                                            return <span className={`${baseClasses} bg-blue-100 text-blue-800`}>Completed</span>;
                                                        default:
                                                            return <span className={`${baseClasses} bg-gray-100 text-gray-800`}>{status}</span>;
                                                    }
                                                };

                                                return (
                                                    <tr key={booking.id || Math.random()} className="hover:bg-gray-50 transition-colors">
                                                        <td className="px-6 py-4 text-sm">
                                                            <div className="font-medium text-gray-900">{booking.customer_name || 'N/A'}</div>
                                                            {booking.customer_email && (
                                                                <div className="text-gray-500 text-xs">{booking.customer_email}</div>
                                                            )}
                                                            {booking.customer_phone && (
                                                                <div className="text-gray-500 text-xs">{booking.customer_phone}</div>
                                                            )}
                                                        </td>
                                                        <td className="px-6 py-4 text-sm text-gray-600">{booking.services?.name || 'N/A'}</td>
                                                        <td className="px-6 py-4 text-sm text-gray-600">
                                                            {booking.staff ? `${booking.staff.first_name || ''} ${booking.staff.last_name || ''}`.trim() || 'N/A' : 'N/A'}
                                                        </td>
                                                        <td className="px-6 py-4 text-sm text-gray-600">{startTime}</td>
                                                        <td className="px-6 py-4 text-sm text-gray-600">{endTime}</td>
                                                        <td className="px-6 py-4 text-sm">
                                                            {getStatusBadge(booking.status)}
                                                        </td>
                                                        <td className="px-6 py-4 text-sm">
                                                            <div 
                                                                ref={(el) => { dropdownRefs.current[booking.id] = el; }}
                                                                className="relative inline-block text-left"
                                                            >
                                                                <button
                                                                    type="button"
                                                                    onClick={() => setOpenDropdown(openDropdown === booking.id ? null : booking.id)}
                                                                    disabled={isUpdating}
                                                                    className="inline-flex items-center px-3 py-1.5 border border-gray-300 shadow-sm text-xs font-medium rounded-lg text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
                                                                >
                                                                    Actions
                                                                    <svg className="-mr-1 ml-2 h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
                                                                        <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd" />
                                                                    </svg>
                                                                </button>
                                                                {openDropdown === booking.id && (
                                                                    <div className="absolute right-0 mt-2 w-48 rounded-md shadow-lg bg-white ring-1 ring-black ring-opacity-5 z-10">
                                                                        <div className="py-1">
                                                                            <button
                                                                                onClick={() => {
                                                                                    setOpenDropdown(null);
                                                                                    openEditModal(booking);
                                                                                }}
                                                                                disabled={isUpdating || booking.status === 'cancelled' || booking.status === 'completed'}
                                                                                className="block w-full text-left px-4 py-2 text-xs text-gray-700 hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
                                                                            >
                                                                                Edit / Reschedule
                                                                            </button>
                                                                            {booking.status !== 'confirmed' && booking.status !== 'cancelled' && booking.status !== 'completed' && (
                                                                                <button
                                                                                    onClick={() => {
                                                                                        setOpenDropdown(null);
                                                                                        confirmBooking(booking.id);
                                                                                    }}
                                                                                    disabled={isUpdating}
                                                                                    className="block w-full text-left px-4 py-2 text-xs text-green-700 hover:bg-green-50 disabled:opacity-50 disabled:cursor-not-allowed"
                                                                                >
                                                                                    Confirm
                                                                                </button>
                                                                            )}
                                                                            <button
                                                                                onClick={() => {
                                                                                    setOpenDropdown(null);
                                                                                    cancelBooking(booking.id);
                                                                                }}
                                                                                disabled={isUpdating || booking.status === 'cancelled' || booking.status === 'completed'}
                                                                                className="block w-full text-left px-4 py-2 text-xs text-red-700 hover:bg-red-50 disabled:opacity-50 disabled:cursor-not-allowed"
                                                                            >
                                                                                Cancel / Delete
                                                                            </button>
                                                                        </div>
                                                                    </div>
                                                                )}
                                                            </div>
                                                        </td>
                                                    </tr>
                                                );
                                            } catch (e) {
                                                console.error("Error rendering booking row:", e, booking);
                                                return null;
                                            }
                                        })}
                                    </tbody>
                                </table>
                            ) : (
                                <div className="p-12 text-center">
                                    <p className="text-gray-500 text-lg">
                                        {bookings.length === 0 
                                            ? 'No bookings found' 
                                            : 'No bookings match your filters'}
                                    </p>
                                    <p className="text-gray-400 text-sm mt-2">
                                        {bookings.length === 0
                                            ? 'Bookings will appear here once customers make appointments'
                                            : 'Try adjusting your filters'}
                                    </p>
                                </div>
                            )}
                        </div>
                    </div>

            {/* Edit/Reschedule Modal */}
            {showEditModal && editingBooking && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white rounded-xl shadow-xl p-6 w-full max-w-md">
                        <h2 className="text-2xl font-bold text-gray-800 mb-4">Edit / Reschedule Booking</h2>
                        <div className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">Start Time</label>
                                <input
                                    type="datetime-local"
                                    value={editStartTime}
                                    onChange={(e) => setEditStartTime(e.target.value)}
                                    className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">End Time</label>
                                <input
                                    type="datetime-local"
                                    value={editEndTime}
                                    onChange={(e) => setEditEndTime(e.target.value)}
                                    className="w-full border border-gray-300 rounded-lg px-4 py-2 focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none"
                                />
                            </div>
                        </div>
                        <div className="flex gap-4 mt-6">
                            <button
                                onClick={saveReschedule}
                                disabled={updatingStatus === editingBooking.id}
                                className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-lg font-semibold hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                            >
                                {updatingStatus === editingBooking.id ? 'Saving...' : 'Save Changes'}
                            </button>
                            <button
                                onClick={() => {
                                    setShowEditModal(false);
                                    setEditingBooking(null);
                                    setEditStartTime('');
                                    setEditEndTime('');
                                }}
                                className="flex-1 bg-gray-200 text-gray-700 py-2 px-4 rounded-lg font-semibold hover:bg-gray-300 transition-colors"
                            >
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
};

export default BookingsPage;
